import React, { Component } from 'react';
import './Styles/Diary.css';
import { updateRenderState } from '.';
import { userData, data, storeData } from './Data';
import { S } from 'selector_dom';
import html2canvas from 'html2canvas';

import MainPage from './NavicationBar/MainPage';
import NavicationBar from './NavicationBar/NavicationBar';
import ItemCard from './Foods/ItemCard';
import SearchPage from './SearchPage/SearchPage';
import CreateMeal from './Meals/CreateMeal';
import MealDetails from './Meals/MealDetails';

export default class Diary extends Component {
  render() {
    return (
      <div id='pageBody'>
        <MealComponent
          key={Math.random()}
          date={this.props.date}
          title='الفطور'
          mealId='breakfast'
          png={require('./assets/bread.png')}
        />
        <MealComponent
          key={Math.random()}
          date={this.props.date}
          title='الغداء'
          mealId='lunch'
          png={require('./assets/soup.png')}
        />
        <MealComponent
          key={Math.random()}
          date={this.props.date}
          title='العشاء'
          mealId='dinner'
          png={require('./assets/fish.png')}
        />
        <MealComponent
          key={Math.random()}
          date={this.props.date}
          title='وجبة خفيفة'
          mealId='snacks'
          png={require('./assets/banana.png')}
        />
      </div>
    );
  }
}

class MealComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isEaten: this.checkEaten(),
    };
  }

  // check if the meal is eaten.
  checkEaten = () =>
    userData.diary.filter(e => e.date === this.props.date)[0]?.eaten?.includes(this.props.mealId) ? true : false;

  // set the meal eaten status.
  eatenMeal = () => {
    const getData = userData.diary.filter(e => e.date === this.props.date)[0];
    if (!getData.eaten) getData.eaten = [];
    // set the meal as not eaten.
    if (getData.eaten.includes(this.props.mealId)) {
      getData.eaten.splice(getData.eaten.indexOf(this.props.mealId), 1);
      this.setState({ isEaten: this.checkEaten() });
      if (getData.eaten.length === 0) delete getData.eaten;
      // set the meal as eaten.
    } else {
      // eaten text animation.
      const textEl = S('#eatenAnim-' + this.props.mealId);
      textEl.css({ display: 'block' });
      setTimeout(() => textEl.css({ display: 'none' }), 500);

      getData.eaten.push(this.props.mealId);
      this.setState({ isEaten: this.checkEaten() });
    }
    // store data to the cloud or to the localStorage if the user is a guest.
    storeData();
  };

  // get meal data and return a card components for it.
  renderMeal = () => {
    const getDataByDate = userData.diary.filter(e => e.date === this.props.date)[0];
    if (getDataByDate?.[this.props.mealId]) {
      const getDataById = getDataByDate[this.props.mealId].map(
        e =>
          (e =
            data.filter(el => el.id === e[0]).length > 0
              ? Object.assign({}, data.filter(el => el.id === e[0])[0], {
                  value: e[1],
                  input: e[2],
                })
              : data[0])
      );
      return getDataById.map(e => (
        <ItemCard
          key={e.id + Math.random() * 1000}
          name={e.name}
          kcal={Math.round((e.kcal / 100) * (e.value * e.input))}
          serving={e.value === 1 ? e.input + ' غرام ' : e.input + '  ' + e.serving.filter(el => el[1] === e.value)[0][0]}
          id={e.id}
          brand={e.brand}
          description={e.description}
          value={e.value}
          input={e.input}
          mealName={this.props.mealId}
        />
      ));
    }
  };

  // claculate meal calories.
  mealKcalSum = () => {
    const getDataByDate = userData.diary.filter(e => e.date === this.props.date)[0];
    if (getDataByDate && getDataByDate[this.props.mealId]) {
      const getDataById = getDataByDate[this.props.mealId].map(
        e => (e = Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] }))
      );
      return Math.round(getDataById.map(e => e.kcal * ((e.value * e.input) / 100)).reduce((a, b) => a + b));
    }
  };

  // open/close three dots menu.
  mealDotsHandle = () => {
    const popId = `#${this.props.mealId}PopUp`;
    const isScale1 = S(popId).getStyle('transform').includes('1');
    S('.mealPopUp').css({ transform: 'scale(0)' });
    if (isScale1) {
      S(popId).css({ transform: 'scale(0)' });
    } else {
      S(popId).css({ transform: 'scale(1)' });
      document.addEventListener(
        'click',
        e => {
          if (
            e.target.className !== 'newMealPopUp' &&
            e.target.id !== 'breakfastPopUpSvg' &&
            e.target.id !== 'lunchPopUpSvg' &&
            e.target.id !== 'dinnerPopUpSvg' &&
            e.target.id !== 'snacksPopUpSvg'
          ) {
            S('.mealPopUp').css({ transform: 'scale(0)' });
          }
        },
        { once: true }
      );
    }
  };

  // create a new meal from dairy items.
  createMeal = () => {
    const getData = [...userData.diary.filter(e => e.date === this.props.date)[0][this.props.mealId]];
    userData.favorite.tmpMeal.mealContent = getData;
    userData.favorite.tmpMeal.mealName = '';
    updateRenderState(<CreateMeal key='CreateMeal' doAnimation={true} goBackOnce={true} />);
    window.history.pushState('', '', '/CreateNewMeal');
  };

  // delete all food items in dairy meal.
  deleteAll = () => {
    const getDataByDate = userData.diary.filter(e => e.date === this.props.date)[0];
    const hasEaten = getDataByDate?.eaten?.includes(this.props.mealId);
    if (hasEaten) {
      const index = getDataByDate.eaten.indexOf(this.props.mealId);
      getDataByDate.eaten.splice(index, 1);
    }
    delete getDataByDate[this.props.mealId];
    updateRenderState([<MainPage key='MainPage' doAnimation={false} />, <NavicationBar key='NavicationBar' pageId='Main' />]);
    storeData();
  };

  // convert the meal component to png and share it.
  screenShotShare = () => {
    window.scrollTo({ top: 0 });
    S('.mealPopUp').css({ transform: 'scale(0)' });
    S('.mealName').css({ boxShadow: 'none' });
    S('body').css({ letterSpacing: '0px' });
    html2canvas(document.querySelector('#' + this.props.mealId + 'Container'), {
      onclone: () => {
        S('.mealName').removeAttr('style');
        S('body').removeAttr('style');
      },
    }).then(canvas => {
      canvas.toBlob(blob => {
        var file = new File([blob], 'share.png', { type: 'image/png', lastModified: Date.now() });

        if (navigator.canShare && navigator.canShare({ files: [file] })) {
          navigator
            .share({
              files: [file],
              title: document.querySelector('title').innerHTML,
              text: window.location.origin,
            })
            .then(() => console.log('Share was successful.'))
            .catch(error => console.log('Sharing failed', JSON.stringify(error)));
        } else {
          console.log(`Your system doesn't support sharing files.`);
        }
      });
    });
  };

  mountSearchComponent = () => {
    updateRenderState(<SearchPage key='SearchPage' title={this.props.title} doAnimation={true} />);
    window.history.pushState('', '', '/search#' + this.props.mealId);
  };

  render() {
    return (
      <div className='mealFoods' id={this.props.mealId + 'Container'}>
        <div className='mealName'>
          <h3>{this.props.title}</h3>
          <svg
            style={{ display: this.renderMeal() ? 'block' : 'none' }}
            onClick={this.mealDotsHandle}
            id='snacksPopUpSvg'
            xmlns='http://www.w3.org/2000/svg'
            viewBox='0 0 24 24'
          >
            <path d='M12 18c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3z' />
          </svg>
          <div className='mealPopUp' id={this.props.mealId + 'PopUp'}>
            <p onClick={this.createMeal} className='newMealPopUp'>
              إنشاء وجبة
            </p>
            <p
              className='newMealPopUp'
              onClick={() => {
                updateRenderState(<MealDetails key='MealDetails' mealName={this.props.mealId} date={this.props.date} />);
                window.history.pushState('', '', '/MealDetails');
              }}
            >
              تفاصيل الوجبة
            </p>
            <p className='newMealPopUp' onClick={this.deleteAll}>
              حذف المحتويات
            </p>
            <p className='newMealPopUp' style={{ display: navigator.canShare ? 'block' : 'none' }} onClick={this.screenShotShare}>
              مشاركة الوجبة
            </p>
          </div>
        </div>
        <div
          className='mealFoodsImgP'
          onClick={this.mountSearchComponent}
          style={{ display: this.renderMeal() ? 'none' : 'block' }}
        >
          <img src={this.props.png} alt={this.props.mealId} />
          <p className='addYourFoodText'>أضف طعامك</p>
        </div>
        {this.renderMeal()}
        <div className='mealKcalSum' style={{ display: this.renderMeal() ? 'flex' : 'none' }}>
          <p>{this.mealKcalSum()} سعرة حرارية</p>
          <div
            className='mealEaten'
            data-html2canvas-ignore='true'
            style={{
              backgroundColor: this.state.isEaten ? 'var(--main-color)' : 'var(--background-kinda-white)',
              border: this.state.isEaten ? 'none' : 'solid 1px lightgray',
            }}
            onClick={this.eatenMeal}
          >
            <svg
              xmlns='http://www.w3.org/2000/svg'
              viewBox='0 0 24 24'
              style={{ fill: this.state.isEaten ? 'white' : 'lightgray' }}
            >
              <path d='M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z' />
            </svg>
            <p id={'eatenAnim-' + this.props.mealId} className='mealEatenText'>
              صحة وهناء
            </p>
          </div>
        </div>
      </div>
    );
  }
}
