import React, { Fragment, Component } from 'react';
import '../Styles/RecentlyAdded.css';
import { userData, data, storeData } from '../Data';
import { dateGetter, dateIndex } from '../NavicationBar/MainPage';
import { Snackbar, snackbarHandle, introPageTransition, introPageFadeTransition } from '../dependencies';
import ItemCard from '../Foods/ItemCard';

let mealValueTmp = '';
export default class RecentlyAdded extends Component {
  state = {
    // set meal name from temporary meal name variable or from the hash.
    meal:
      mealValueTmp.length > 0
        ? mealValueTmp
        : window.location.hash.split('#')[1]
        ? window.location.hash.split('#')[1]
        : 'breakfast',
  };

  componentDidMount() {
    // do a transition animation from bottom to the top or do a fade transition.
    this.props.doAnimation ? introPageTransition('recentPage') : introPageFadeTransition('recentPage');

    // empty temporary meal name variable when enter to search or main page.
    window.addEventListener('popstate', () => {
      const path = window.location.pathname;
      if (path === '/' || path.includes('search')) mealValueTmp = '';
    });
  }

  /**
   * get recent food items components for a given meal in specefic date.
   * @param {'breakfast' | 'dinner' | 'lunch' | 'snacks'} mealName
   * @param {Number} date
   */
  getRecentMeal = (mealName, date) => {
    const getDataByDate = userData.diary.filter(e => e.date === dateGetter(date))[0];
    if (getDataByDate && getDataByDate[mealName]) {
      const getDataById = getDataByDate[mealName].map(
        e =>
          (e =
            data.filter(el => el.id === e[0]).length > 0
              ? Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] })
              : data[0])
      );
      return getDataById.map(e => (
        <ItemCard
          key={e.id + Math.random() * 1000}
          name={e.name}
          kcal={Math.round((e.kcal / 100) * (e.value * e.input))}
          serving={e.value === 1 ? e.input + ' غرام ' : e.input + '  ' + e.serving.filter(el => el[1] === e.value)[0][0]}
          id={e.id}
          brand={e.brand}
          description={e.description}
          value={e.value}
          input={e.input}
        />
      ));
    }
  };

  /**
   * add all recent food items in specefic date button handel.
   * @param {'breakfast' | 'dinner' | 'lunch' | 'snacks'} toAddMeal
   * add to this meal.
   * @param {'breakfast' | 'dinner' | 'lunch' | 'snacks'} mealName
   * add from this meal.
   * @param {Number} date
   */
  addAllButtonHandle = (toAddMeal, mealName, date) => {
    const getDataByDate = userData.diary.filter(e => e.date === dateGetter(date))[0];
    let itemsArray;
    if (getDataByDate && getDataByDate[mealName]) {
      itemsArray = [...getDataByDate[mealName]];
    }

    const addByDate = userData.diary.filter(e => e.date === dateGetter(dateIndex))[0];
    if (window.location.pathname.includes('CreateNewMeal') || window.location.pathname.includes('EditMeal')) {
      userData.favorite.tmpMeal.mealContent.push(...itemsArray);
      snackbarHandle('تمت الإضافة على الوجبة', 'success');
    } else if (addByDate) {
      addByDate[toAddMeal] ? addByDate[toAddMeal].push(...itemsArray) : (addByDate[toAddMeal] = itemsArray);
      snackbarHandle('تمت الإضافة على ' + this.checkHash(toAddMeal), 'success');
    } else {
      snackbarHandle('تمت الإضافة على ' + this.checkHash(toAddMeal), 'success');
      userData.diary.push({ date: dateGetter(dateIndex), [toAddMeal]: itemsArray });
    }

    // store data to the cloud or the localStorage if user is a guest.
    storeData();
  };

  // render recent added food items for the last 2 weeks
  renderRecent = () => {
    let content = [];
    for (let i = 0; i > -16; i--) {
      if (this.getRecentMeal(this.state.meal, i)) {
        content.push([
          <Fragment key={Math.random() * 1000}>
            <p id='recentDateText'>{dateGetter(i)}</p>
            <p
              className='recentAddAll'
              onClick={() => this.addAllButtonHandle(window.location.hash.split('#')[1], this.state.meal, i)}
            >
              إضافة الكل ↓
            </p>
            <div>{this.getRecentMeal(this.state.meal, i)}</div>
          </Fragment>,
        ]);
      }
    }
    return content;
  };

  // meal select menu on change handle.
  selectMealHandle = e => {
    this.setState({ meal: e.target.value });
    mealValueTmp = e.target.value;
  };

  // translate the meal name from hash to arabic for the title.
  checkHash = h => {
    let hash;
    switch (h) {
      case 'breakfast':
        hash = 'الفطور';
        break;
      case 'lunch':
        hash = 'الغداء';
        break;
      case 'dinner':
        hash = 'العشاء';
        break;
      case 'snacks':
        hash = 'وجبة خفيفة';
        break;
      default:
        break;
    }
    return hash;
  };

  backButtonHandle = () => window.history.back();

  render() {
    return (
      <div id='recentPage'>
        <Snackbar />
        <header id='recentPageHeader'>
          <p id='recentHeaderTitle'>المضافة مؤخراً</p>
          <svg id='recentBack' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' onClick={this.backButtonHandle}>
            <path d='M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z' />
          </svg>
        </header>
        <select id='recentSelectMeal' value={this.state.meal} onChange={this.selectMealHandle}>
          <option className='itemViewerSelectMealItems' value='breakfast'>
            الفطور
          </option>
          <option className='itemViewerSelectMealItems' value='lunch'>
            الغداء
          </option>
          <option className='itemViewerSelectMealItems' value='dinner'>
            العشاء
          </option>
          <option className='itemViewerSelectMealItems' value='snacks'>
            وجبة خفيفة
          </option>
        </select>
        <p id='recentNoResults' style={{ display: this.renderRecent().length > 0 ? 'none' : 'block' }}>
          لا يوجد عناصر مضافة في آخر اسبوع على {this.checkHash(this.state.meal)}
        </p>
        <div>{this.renderRecent()}</div>
      </div>
    );
  }
}
