import React, { Component, Fragment } from 'react';
import ReactDOM from 'react-dom';

import 'firebaseui/dist/firebaseui-rtl.css';
import './Styles/index.css';

import * as serviceWorker from './serviceWorker';

import MainPage from './NavicationBar/MainPage';
import NavicationBar from './NavicationBar/NavicationBar';
import MainPageLoading from './MainPageLoading';
import CategoriesList from './SearchPage/CategoriesList';
import SearchPage from './SearchPage/SearchPage';
import FavoritePage from './NavicationBar/FavoritePage';
import RecentlyAdded from './SearchPage/RecentlyAdded';
import CreateMeal from './Meals/CreateMeal';
import MealViewer from './Meals/MealViewer';

import { onload } from './dependencies';

onload();

export let updateRenderState = function (component) {
  this.setState({ renderPage: component });
};

export default class Index extends Component {
  constructor() {
    super();
    this.state = {
      renderPage: window.localStorage.getItem('isGuest')
        ? [<MainPage key={Math.random() * 100} doAnimation={true} />, <NavicationBar key='NavicationBar' pageId='Main' />]
        : window.localStorage.getItem('expectToBeSignedIn')
        ? [<MainPageLoading key={Math.random() * 100} />]
        : '',
    };
    updateRenderState = updateRenderState.bind(this);
  }

  async componentDidMount() {
    if (window.location.pathname.length !== 1) window.history.replaceState('', '', '/');
    window.addEventListener('popstate', this.checkURL);
    window.resizeBy(350, 800);
  }

  checkURL = () => {
    const [path, hash] = [window.location.pathname, window.location.hash];

    if (path.includes('search') && !hash.includes('MealViewer')) {
      this.setState({ renderPage: <SearchPage key='SearchPage' /> });
    } else if (path.includes('categoriesList')) {
      this.setState({ renderPage: <CategoriesList key={Math.random() * 100} /> });
    } else if (
      path === '/favorites' &&
      !hash.includes('#MealViewer') &&
      (hash.includes('#breakfast') || hash.includes('#lunch') || hash.includes('#dinner') || hash.includes('#snacks'))
    ) {
      this.setState({
        renderPage: <FavoritePage key='FavoritePage' />,
      });
    } else if (
      (path === '/favorites' && hash.length === 0) ||
      (path === '/favorites' && hash.includes('#Meals') && !hash.includes('#MealViewer'))
    ) {
      this.setState({
        renderPage: [<FavoritePage key='FavoritePage' />, <NavicationBar key='NavicationBar' pageId='Favorite' />],
      });
    } else if (path === '/CreateNewMeal/favorites') {
      this.setState({ renderPage: <FavoritePage key='FavoritePage' /> });
    } else if (path === '/CreateNewMeal') {
      this.setState({ renderPage: <CreateMeal key='CreateMeal' /> });
    } else if (path === '/CreateNewMeal/recentlyAdded') {
      this.setState({ renderPage: <RecentlyAdded key='RecentlyAdded' /> });
    } else if (path === '/EditMeal/favorites') {
      this.setState({ renderPage: <FavoritePage key='FavoritePage' /> });
    } else if (path === '/EditMeal') {
      this.setState({ renderPage: <CreateMeal key='CreateMeal' editMode={true} /> });
    } else if (path === '/EditMeal/recentlyAdded') {
      this.setState({ renderPage: <RecentlyAdded key='RecentlyAdded' /> });
    } else if (hash.includes('MealViewer')) {
      this.setState({ renderPage: <MealViewer key='MealViewer' /> });
    } else if (path === '/recentlyAdded') {
      this.setState({ renderPage: <RecentlyAdded key='RecentlyAdded' /> });
    } else if (path === '/') {
      this.setState({
        renderPage: [<MainPage key='MainPage' doAnimation={true} />, <NavicationBar key='NavicationBar' pageId='Main' />],
      });
    }
  };

  render() {
    return <Fragment>{this.state.renderPage}</Fragment>;
  }
}
ReactDOM.render(<Index />, document.getElementById('root'));
serviceWorker.register();
