import React, { Component } from 'react';
import '../Styles/CategoriesList.css';
import { wait, introPageFadeTransition } from '../dependencies';
import { S } from 'selector_dom';
import { data } from '../Data';

import ItemCard from '../Foods/ItemCard';

const foodCategories = [
  'أسماك',
  'البيض ومنتجات الحليب',
  'بقوليات',
  'بذور',
  'حبوب',
  'البسكوت والحلويات',
  'خضار',
  'الدبس والصلصات',
  'الزيت والدهون',
  'فاكهة',
  'لحم بقري',
  'لحوم الدواجن',
  'لحم الشاة',
  'لحوم مصنعة',
  'معجنات',
  'مكسرات',
  'مكملات غذائية',
];

export default class CategoriesList extends Component {
  constructor() {
    super();
    this.state = {
      renderList: this.mapListHandle(foodCategories),
    };
  }

  componentDidMount() {
    // used when go back to categories page.
    const listNum =
      window.location.pathname.includes('CreateNewMeal') || window.location.pathname.includes('EditMeal')
        ? Number(window.location.hash.split('#')[1])
        : Number(window.location.hash.split('#')[2]);

    // open the same list when go back to categories page.
    if (!isNaN(listNum)) {
      this.onClickListHandle(foodCategories[listNum]);
      introPageFadeTransition('categoriesListPage');
    } else {
      S('#categoriesListPageHeaderTitle').text('الأصناف');
      // on loads list animation.
      const anim = async () => {
        S('#categoriesListBody li').css({ transition: '0' });
        S('#categoriesListBody li').css({ transform: 'scaleY(0)' });
        await wait(20);
        S('#categoriesListBody li').removeAttr('style');
      };
      anim();
    }
  }

  /**
   * map food categories list to list elements.
   * @param {foodCategories} arr
   */
  mapListHandle = arr =>
    arr.map(e => (
      <li key={Math.random() * 100} onClick={() => this.onClickListHandle(e)}>
        {e}
      </li>
    ));

  // cateogrie on click handle will open a list of food items.
  onClickListHandle = async label => {
    const listData = data.filter(e => e.description === label);
    const renderlist = listData
      .sort((a, b) => {
        const nameA = a.name.toUpperCase();
        const nameB = b.name.toUpperCase();
        if (nameA < nameB) return -1;
        if (nameA > nameB) return 1;
        return 0;
      })
      .map(e => (
        <ItemCard
          key={e.id}
          name={e.name}
          kcal={e.serving ? Math.round((e.kcal / 100) * e.serving[0][1]) : e.kcal}
          serving={e.serving ? e.serving[0][0] : 'كل (100) غرام'}
          id={e.id}
          brand={e.brand}
          description={e.description}
          scale={true}
        />
      ));

    // used when go back to categories page.
    const listNum =
      window.location.pathname.includes('CreateNewMeal') || window.location.pathname.includes('EditMeal')
        ? Number(window.location.hash.split('#')[1])
        : Number(window.location.hash.split('#')[2]);

    // animation
    if (isNaN(listNum)) {
      S('#categoriesListBody li').css({ transform: 'scaleY(0)' });
      await wait(200);
    }

    this.setState({ renderList: renderlist });

    S('#categoriesListPageHeaderTitle').text(label);

    if (isNaN(listNum)) {
      window.scrollTo({ top: 0 });
      const forHash = foodCategories.findIndex(e => e === label);
      if (window.location.pathname.includes('CreateNewMeal')) {
        window.history.pushState('', '', '/CreateNewMeal/categoriesList#' + forHash);
      } else if (window.location.pathname.includes('EditMeal')) {
        window.history.pushState('', '', '/EditMeal/categoriesList#' + forHash);
      } else {
        window.history.pushState('', '', '/categoriesList' + window.location.hash + '#' + forHash);
      }
    }
  };

  backButtonHandle = () => window.history.back();

  render() {
    return (
      <div id='categoriesListPage'>
        <header id='categoriesListPageHeader'>
          <svg id='categoriesListPageBack' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' onClick={this.backButtonHandle}>
            <path d='M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z' />
          </svg>
          <p id='categoriesListPageHeaderTitle'></p>
        </header>
        <ul id='categoriesListBody'>{this.state.renderList}</ul>
      </div>
    );
  }
}
