import React, { Fragment, Component } from 'react';
import './Styles/MainPageLoading.css';
import { S } from 'selector_dom';

export default class MainPageLoading extends Component {
  componentDidMount() {
    this.setRemainPercent();
    this.responsiveResizing();
    document.getElementsByTagName('body')[0].style.overflow = 'hidden';
  }
  componentWillUnmount() {
    document.getElementsByTagName('body')[0].removeAttribute('style');
  }

  setRemainPercent = () => {
    S('#remainProgress').progress({
      input: 0,
      strokeWidth: 6,
      strokeBackgroundColor: 'rgba(255, 255, 255, 0.3)',
      textColor: 'rgba(255, 255, 255, 0.3)',
      textStyle: { fontFamily: '"Cairo", sans-serif' },
      strokeColor: 'rgba(255, 255, 255, 0.3)',
      animation: false,
    });
  };
  responsiveResizing = () => {
    // for macros percentage bars position from the bottom
    const getHeaderTitleHeight = S('#title').getStyle('height', true);
    const getHeaderPercentNumHeight = S('.percentNum').getStyle('height', true);
    const calcBottom = getHeaderTitleHeight + getHeaderPercentNumHeight + 38 + 'px';
    S('#macrosPercent').css({ bottom: calcBottom });
  };

  render() {
    return (
      <Fragment>
        <div id='MainPage' style={{ opacity: 1 }}>
          <h2 id='title'>سعراتي اليومية</h2>
          <div id='header'>
            <svg
              width='100%'
              viewBox='0 0 320 300'
              style={{
                animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite',
                filter: 'drop-shadow(0px 0px 5px rgb(0, 0, 0, 0.5))',
              }}
            >
              <linearGradient id='Gradient1' gradientTransform='rotate(45)'>
                <stop offset='20%' />
                <stop offset='100%' />
              </linearGradient>
              <path d='M0 0H320V286.491C320 286.491 249 300 159 300C69 300 0 286.491 0 286.491V0Z' fill='url(#Gradient1)' />
            </svg>
            <div className='remainValues'>
              <div className='caloriesEaten'>
                <p className='txtLoadingAnim' style={{ width: '40px', color: 'rgb(0,0,0,0)', display: 'inline-block' }}>
                  0
                </p>
                <p style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}>الـمـأكـولة</p>
              </div>
              <div id='remainProgress' style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}></div>
              <div className='caloriesRemain'>
                <p className='txtLoadingAnim' style={{ width: '40px', color: 'rgb(0,0,0,0)', display: 'inline-block' }}>
                  0
                </p>
                <p style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}>المتبـقية</p>
              </div>
            </div>
            <div id='macrosPercent'>
              <div className='macrosPercentContainer txtLoadingAnim'>
                <p className='macroName'>الكربوهيدرات</p>
                <p
                  className='percentNum'
                  style={{
                    width: '60px',
                    color: 'rgb(0,0,0,0)',
                    backgroundColor: 'rgba(255, 255, 255, 0.3)',
                    display: 'inline-block',
                    borderRadius: '10px',
                  }}
                >
                  باقي
                </p>
              </div>
              <div className='macrosPercentContainer txtLoadingAnim'>
                <p className='macroName'>البروتين</p>
                <p
                  className='percentNum'
                  style={{
                    width: '60px',
                    color: 'rgb(0,0,0,0)',
                    backgroundColor: 'rgba(255, 255, 255, 0.3)',
                    display: 'inline-block',
                    borderRadius: '10px',
                  }}
                >
                  باقي
                </p>
              </div>
              <div className='macrosPercentContainer txtLoadingAnim'>
                <p className='macroName'>الدهون</p>
                <p
                  className='percentNum'
                  style={{
                    width: '60px',
                    color: 'rgb(0,0,0,0)',
                    backgroundColor: 'rgba(255, 255, 255, 0.3)',
                    display: 'inline-block',
                    borderRadius: '10px',
                  }}
                >
                  باقي
                </p>
              </div>
            </div>
          </div>
          <div id='dateSelector'>
            <svg
              id='dateToRightArrow'
              className='dateArrow'
              xmlns='http://www.w3.org/2000/svg'
              viewBox='0 0 24 24'
              style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}
            >
              <path d='M5 3l3.057-3 11.943 12-11.943 12-3.057-3 9-9z' />
            </svg>
            <p style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}>اليوم</p>
            <svg
              id='dateToLeftArrow'
              className='dateArrow'
              xmlns='http://www.w3.org/2000/svg'
              viewBox='0 0 24 24'
              transform='rotate(180)'
              style={{ transform: 'rotate(180deg)', animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}
            >
              <path d='M5 3l3.057-3 11.943 12-11.943 12-3.057-3 9-9z' />
            </svg>
          </div>
          <div
            id='tipsContainer'
            style={{
              animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite',
              display: window.localStorage.getItem('tips') === 'true' ? 'block' : 'none',
            }}
          >
            <svg
              xmlns='http://www.w3.org/2000/svg'
              viewBox='0 0 24 24'
              style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}
            >
              <path d='M24 20.188l-8.315-8.209 8.2-8.282-3.697-3.697-8.212 8.318-8.31-8.203-3.666 3.666 8.321 8.24-8.206 8.313 3.666 3.666 8.237-8.318 8.285 8.203z' />
            </svg>
            <p className='txtLoadingAnim' style={{ color: 'rgb(0,0,0,0)', display: 'inline-block' }}>
              نــصــائــح عــامــة
            </p>
            <div className='tipsLinesLoading'></div>
            <div className='tipsLinesLoading'></div>
            <div className='tipsLinesLoading'></div>
            <div className='tipsLinesLoading'></div>
            <div className='tipsLinesLoading'></div>
            <div className='tipsLinesLoading'></div>
            <div className='tipsLinesLoading'></div>
            <div className='tipsLinesLoading'></div>
            <div className='tipsLinesLoading'></div>
          </div>
          <div id='diaryContainer'>
            <div id='pageBody'>
              <div className='mealFoods' id='breakfastContainer'>
                <div className='mealName'>
                  <h3 style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}>الفطور</h3>
                </div>
                <div className='mealFoodsImgP' style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}>
                  <img
                    src={require('./assets/bread.png')}
                    alt='bread'
                    style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}
                  />
                  <p className='addYourFoodText' style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}>
                    أضف طعامك
                  </p>
                </div>
              </div>
              <div className='mealFoods' id='breakfastContainer'>
                <div className='mealName'>
                  <h3 style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}>الغداء</h3>
                </div>
                <div className='mealFoodsImgP' style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}>
                  <img src={require('./assets/soup.png')} alt='soup' />
                  <p className='addYourFoodText' style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}>
                    أضف طعامك
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <nav id='navicationBar'>
          <div className='navElements' id='navMainPageButton'>
            <svg
              xmlns='http://www.w3.org/2000/svg'
              viewBox='0 0 24 24'
              style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}
            >
              <path d='M4 22h-4v-4h4v4zm0-12h-4v4h4v-4zm0-8h-4v4h4v-4zm3 0v4h17v-4h-17zm0 12h17v-4h-17v4zm0 8h17v-4h-17v4z' />
            </svg>
          </div>
          <div className='navElements' id='navFavoritePageButton'>
            <svg
              xmlns='http://www.w3.org/2000/svg'
              viewBox='0 0 24 24'
              style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}
            >
              <path d='M6.28 3c3.236.001 4.973 3.491 5.72 5.031.75-1.547 2.469-5.021 5.726-5.021 2.058 0 4.274 1.309 4.274 4.182 0 3.442-4.744 7.851-10 13-5.258-5.151-10-9.559-10-13 0-2.676 1.965-4.193 4.28-4.192zm.001-2c-3.183 0-6.281 2.187-6.281 6.192 0 4.661 5.57 9.427 12 15.808 6.43-6.381 12-11.147 12-15.808 0-4.011-3.097-6.182-6.274-6.182-2.204 0-4.446 1.042-5.726 3.238-1.285-2.206-3.522-3.248-5.719-3.248z' />
              <path
                id='navFavouriteButtonFill'
                fill='none'
                d='M12 4.248c-3.148-5.402-12-3.825-12 2.944 0 4.661 5.571 9.427 12 15.808 6.43-6.381 12-11.147 12-15.808 0-6.792-8.875-8.306-12-2.944z'
              />
            </svg>
          </div>
          <div className='navElements' id='navSummaryPageButton'>
            <svg
              xmlns='http://www.w3.org/2000/svg'
              viewBox='0 0 24 24'
              style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}
            >
              <path d='M5 19h-4v-8h4v8zm6 0h-4v-18h4v18zm6 0h-4v-12h4v12zm6 0h-4v-4h4v4zm1 2h-24v2h24v-2z' />
            </svg>
          </div>
          <div className='navElements' id='navMeasurePageButton'>
            <svg
              xmlns='http://www.w3.org/2000/svg'
              viewBox='0 0 24 24'
              style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}
            >
              <path d='M19 0h-14c-2.762 0-5 2.239-5 5v14c0 2.761 2.238 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm3 19c0 1.302-.839 2.401-2 2.816v-2.816h-1v3h-2v-3h-1v3h-2v-3h-1v3h-2v-3h-1v3h-2v-3h-1v3h-2v-3h-1v2.815c-1.161-.414-2-1.513-2-2.815v-14c0-1.302.839-2.401 2-2.816v2.816h1v-3h2v6h1v-6h2v3h1v-3h2v3h1v-3h2v6h1v-6h2v3h1v-2.816c1.161.415 2 1.514 2 2.816v14zm-14.141-9h1.141v6h-1.354v-3.889h-1.488v-1c.893-.041 1.701-.201 1.701-1.111zm8.315 4.667h1.826v1.306h-3.922v-.958c1.529-1.491 2.379-2.244 2.389-3.018 0-.462-.268-.717-.754-.717-.453 0-.816.245-1.168.524l-.424-1.148c.545-.452 1.191-.656 1.833-.656 1.179 0 1.941.74 1.941 1.885.001 1.155-.899 2.043-1.721 2.782z' />
            </svg>
          </div>
          <div className='navElements' id='navSettingsPageButton'>
            <span></span>
            <svg
              xmlns='http://www.w3.org/2000/svg'
              viewBox='0 0 24 24'
              style={{ animation: 'loadingPreivew 1.5s ease-in-out 0.5s infinite' }}
            >
              <path d='M6 18h-2v5h-2v-5h-2v-3h6v3zm-2-17h-2v12h2v-12zm11 7h-6v3h2v12h2v-12h2v-3zm-2-7h-2v5h2v-5zm11 14h-6v3h2v5h2v-5h2v-3zm-2-14h-2v12h2v-12z' />
            </svg>
          </div>
        </nav>
      </Fragment>
    );
  }
}
