import React, { Component } from 'react';
import '../Styles/FavoritePage.css';
import { wait, introPageTransition, introPageFadeTransition } from '../dependencies';
import { requestFrame, S } from 'selector_dom';
import { userData, data } from '../Data';
import ItemCard from '../Foods/ItemCard';
import MealCard from '../Meals/MealCard';
import lottie from 'lottie-web';
import lottieNoResults from '../assets/lottieNoResults.json';
import lottieEmpty from '../assets/lottieEmpty.json';

export default class FavoritePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      foodRender: this.renderFoodData(),
      mealRender: this.renderMealData(),
    };
  }

  foodHeight = 'auto';
  mealHeight = 'auto';

  componentDidMount() {
    window.scrollTo({ top: 0 });
    // do a transition animation from bottom to the top or do a fade transition.
    this.props.doAnimation ? introPageTransition('favoritePage') : this.pageFade();
    // show an animation when the food/meal tab is empty.
    this.renderLottie();
    // set static variables only if the food/meal tab is not empty.
    if (this.renderFoodData().length !== 0) this.foodHeight = S('#favoritePageBodyFood').getStyle('height');
    if (this.renderMealData().length !== 0) this.mealHeight = S('#favoritePageBodymeal').getStyle('height');

    const container = S('#favoritePageBodyContainer');
    const containerEl = document.getElementById('favoritePageBodyContainer');
    const containerWidth = container.getStyle('width', true);
    // adjust scroll position on resize.
    if (window.screen.width > 580) S(window).on('resize', this.onResizeHandle);

    // Switch to meal tab.
    if (this.props.page === 2 || window.location.hash.includes('#Meals')) {
      // set the height of the container to remove extra space in meal tab.
      container.css({ height: this.mealHeight });
      // play empty meal animation, show only when the tab is empty.
      lottie.play('lottieEmptyMeal');
      // move the white bar indicator to the left.
      S('#paddle').css({ right: '50%' });
      // scroll to meal tab.
      containerEl.scrollTo({ left: -containerWidth });
      // hide the search and sort icons if the meal tab is empty.
      if (this.renderMealData().length === 0) {
        S('#favoritePageSort').css({ display: 'none' });
        S('#favoriteSearchSvg').css({ display: 'none' });
      } else {
        S('#favoritePageSort').css({ display: 'block' });
        S('#favoriteSearchSvg').css({ display: 'block' });
      }

      if (this.props.page === 2) window.history.replaceState('', '', '/favorites#Meals');
    } else {
      // set the height of the container to remove extra space in food tab.
      container.css({ height: this.foodHeight });
      // hide the search and sort icons if the food tab is empty.
      if (this.renderFoodData().length === 0) {
        S('#favoritePageSort').css({ display: 'none' });
        S('#favoriteSearchSvg').css({ display: 'none' });
      } else {
        S('#favoritePageSort').css({ display: 'block' });
        S('#favoriteSearchSvg').css({ display: 'block' });
      }
    }
  }

  componentWillUnmount() {
    // dispatch event listener to avoid errors.
    S(window).off('resize', this.onResizeHandle);
  }

  /**
   * returns food items to be rendered in food tab.
   * @param {Boolean} alphabet
   *  sort the food items by alphabet if true.
   */
  renderFoodData = alphabet => {
    let favoritedata = [
      ...userData.favorite.food.map(
        e => (e = data.filter(el => el.id === e).length > 0 ? Object.assign({}, data.filter(el => el.id === e)[0]) : data[0])
      ),
    ];
    // sort by alphabet
    favoritedata = alphabet
      ? favoritedata.sort((a, b) => {
          const nameA = a.name.toUpperCase();
          const nameB = b.name.toUpperCase();
          if (nameA < nameB) return -1;
          if (nameA > nameB) return 1;
          return 0;
        })
      : favoritedata;
    return favoritedata.map(e => (
      <ItemCard
        key={e.id}
        name={e.name}
        kcal={e.serving ? Math.round((e.kcal / 100) * e.serving[0][1]) : e.kcal}
        serving={e.serving ? e.serving[0][0] : 'كل (100) غرام'}
        id={e.id}
        brand={e.brand}
        description={e.description}
      />
    ));
  };

  /**
   * returns meal items to be rendered in meal tab.
   * @param {Boolean} alphabet
   *  sort the meal items by alphabet if true.
   */
  renderMealData = alphabet => {
    let ComponentArray = [];
    let dataCollection = [...userData.favorite.meals];
    // sort by alphabet
    dataCollection = alphabet
      ? dataCollection.sort((a, b) => {
          const nameA = a.mealName.toUpperCase();
          const nameB = b.mealName.toUpperCase();
          if (nameA < nameB) return -1;
          if (nameA > nameB) return 1;
          return 0;
        })
      : userData.favorite.meals;

    for (let i in dataCollection) {
      const mealdata = dataCollection[i].content.map(
        e =>
          (e =
            data.filter(el => el.id === e[0]).length > 0
              ? Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] })
              : data[0])
      );
      const kcalSum = mealdata.map(e => (e.kcal * (e.value * e.input)) / 100).reduce((a, b) => a + b);
      ComponentArray.push(
        <MealCard
          key={Math.random() * 1000}
          name={dataCollection[i].mealName}
          kcal={kcalSum}
          ingredients={dataCollection[i].content.length}
        />
      );
    }
    return ComponentArray;
  };

  // prevent tab scroling when window size is changing.
  onResizeHandle = () => {
    const container = document.getElementById('favoritePageBodyContainer');
    const width = S('#favoritePageBodyContainer').getStyle('width', true);
    if (window.location.hash.includes('Meals')) {
      container.scrollTo({ left: -width });
    } else {
      container.scrollTo({ left: 0 });
    }
  };

  renderLottie = async () => {
    // animation on empty or no search results.
    lottie.loadAnimation({
      container: document.getElementById('lottieEmptyFood'),
      renderer: 'svg',
      loop: false,
      autoplay: true,
      animationData: lottieEmpty,
    });

    lottie.loadAnimation({
      container: document.getElementById('lottieEmptyMeal'),
      renderer: 'svg',
      loop: false,
      autoplay: false,
      animationData: lottieEmpty,
      name: 'lottieEmptyMeal',
    });

    lottie.loadAnimation({
      container: document.getElementById('lottieNoResultsMeal'),
      renderer: 'svg',
      loop: true,
      autoplay: true,
      animationData: lottieNoResults,
    });

    lottie.loadAnimation({
      container: document.getElementById('lottieNoResultsFood'),
      renderer: 'svg',
      loop: true,
      autoplay: true,
      animationData: lottieNoResults,
    });
  };

  switchTabs = async e => {
    const containerEl = document.getElementById('favoritePageBodyContainer');
    const container = S('#favoritePageBodyContainer');
    const containerWidth = container.getStyle('width', true);
    // switch to food tab.
    if (window.location.hash.includes('#Meals') && e.target.id === 'favoritePageFoodsTab') {
      // set container height to auto if the food tab is empty, or set it to a value if it isnt empty to remove extra space.
      if (this.state.foodRender.length === 0 || this.renderFoodData().length === 0) container.css({ height: 'auto' });
      else container.css({ height: this.foodHeight });
      // move the white bar under food tab title.
      S('#paddle').css({ right: '0%' });
      // scroll to the top of the tab.
      window.scrollTo({ top: 0 });
      // scroll to food tab.
      requestFrame(
        {
          from: containerEl.scrollLeft,
          to: containerEl.scrollLeft + containerWidth,
          duration: 300,
          easingFunction: 'easeOutQuart',
        },
        x => containerEl.scrollTo({ left: x })
      );
      // hide or show sort and search buttons if the tab is empty or not.
      if (this.renderFoodData().length === 0) {
        S('#favoritePageSort').css({ display: 'none' });
        S('#favoriteSearchSvg').css({ display: 'none' });
      } else {
        S('#favoritePageSort').css({ display: 'block' });
        S('#favoriteSearchSvg').css({ display: 'block' });
      }
      // manage path and hash
      window.location.hash.includes('Meals') && window.location.hash.length === 6
        ? window.history.replaceState('', '', '/favorites')
        : window.history.replaceState('', '', '/favorites#' + window.location.hash.split('#')[2]);

      // switch to meal tab
    } else if (!window.location.hash.includes('#Meals') && e.target.id === 'favoritePageMealsTab') {
      // set container height to auto if the meal tab is empty, or set it to a value if it isnt empty to remove extra space.
      if (this.state.mealRender.length === 0 || this.renderMealData().length === 0) container.css({ height: 'auto' });
      else container.css({ height: this.mealHeight });
      // play empty meal animation.
      lottie.play('lottieEmptyMeal');
      // move the white bar under meal tab title.
      S('#paddle').css({ right: '50%' });
      // scroll to the top of the tab.
      window.scrollTo({ top: 0 });
      // scroll to meal tab.
      requestFrame(
        {
          from: containerEl.scrollLeft,
          to: containerEl.scrollLeft - containerWidth,
          duration: 300,
          easingFunction: 'easeOutQuart',
        },
        x => containerEl.scrollTo({ left: x })
      );
      // hide or show sort and search buttons if the tab is empty or not.
      if (this.renderMealData().length === 0) {
        S('#favoritePageSort').css({ display: 'none' });
        S('#favoriteSearchSvg').css({ display: 'none' });
      } else {
        S('#favoritePageSort').css({ display: 'block' });
        S('#favoriteSearchSvg').css({ display: 'block' });
      }
      // manage path and hash.
      window.location.hash.length === 0
        ? window.history.replaceState('', '', '/favorites#Meals')
        : window.history.replaceState('', '', '/favorites#Meals#' + window.location.hash.split('#')[1]);
    }
  };

  pageFade = () => {
    if (window.location.pathname === '/favorites' && window.location.hash.length === 0)
      S('#favoritePageBack').css({ display: 'none' });
    introPageFadeTransition('favoritePage');
  };

  backButtonHandle = () => window.history.back();

  // sort icon button handle.
  sortHandle = () => {
    const isScale1 = S('#favoritePagePopUp').getStyle('transform').includes('1');
    S('.mealPopUp').css({ transform: 'scale(0)' });
    if (isScale1) {
      S('#favoritePagePopUp').css({ transform: 'scale(0)' });
    } else {
      S('#favoritePagePopUp').css({ transform: 'scale(1)' });
      document.addEventListener(
        'click',
        e => {
          if (e.target.className !== 'favoritePagePopUpItems') S('#favoritePagePopUp').css({ transform: 'scale(0)' });
        },
        { once: true }
      );
    }
  };

  // show/hide search bar.
  toggleSearch = async () => {
    const el = S('#favoriteSearchContainer');
    const header = S('#favoritePageHeader');
    if (el.getStyle('height', true) > 0) {
      S('#favoriteSearchBar').blur();
      this.searchIconAnimation('close');
      el.animate({}, { height: '0px', padding: '0px' });

      this.setState({ foodRender: this.renderFoodData() });
      this.setState({ mealRender: this.renderMealData() });

      document.getElementById('favoriteSearchBar').value = '';
      await wait(300);
      header.css({ boxShadow: '0px 0px 7px 0px rgba(0, 0, 0, 0.3)' });
    } else {
      this.searchIconAnimation('open');
      S('#favoriteSearchBar').focus();
      el.animate({}, { height: S('#favoriteSearchBar').getStyle('height'), padding: '15px 0px' });
      header.css({ boxShadow: 'unset' });
    }
  };

  searchIconAnimation = state => {
    const circle = S('#favoriteSearchSvg  circle');
    const arm1 = S('#favoriteSearchSvg path:nth-child(2)');
    const arm2 = S('#favoriteSearchSvg path:nth-child(3)');
    const op = { duration: 200, easingFunction: 'easeOutCubic' };
    if (state === 'open') {
      requestFrame({ from: [0, 1, 0], to: [10, 1.2, -30], ...op }, (x, y, z) => {
        circle.css({ transform: `translate(${x}%, ${x}%) scale(${y})`, transformOrigin: 'center' });
        arm1.css({ transform: `translate(${z}%, ${z}%)` });
        arm2.css({ transform: `translate(${z}%, ${z}%)` });
      });
      requestFrame({ to: -90, delay: 200, ...op }, x => {
        arm1.css({ transform: `translate(-30%, -30%) rotate(${x}deg)`, transformOrigin: '77% 77%' });
      });
    } else {
      requestFrame({ from: -90, to: 0, ...op }, x => {
        arm1.css({ transform: `translate(-30%, -30%) rotate(${x}deg)`, transformOrigin: '77% 77%' });
      });
      requestFrame({ from: [10, 1.2, -30], to: [0, 1, 0], delay: 200, ...op }, (x, y, z) => {
        circle.css({ transform: `translate(${x}%, ${x}%) scale(${y})` });
        arm1.css({ transform: `translate(${z}%, ${z}%)` });
        arm2.css({ transform: `translate(${z}%, ${z}%)` });
      });
    }
  };

  searchOnChangeHandle = sort => {
    window.scrollTo({ top: 0 });
    const container = S('#favoritePageBodyContainer');
    const searchInput = document.getElementById('favoriteSearchBar').value;
    const foodSearchData = this.renderFoodData(sort).filter(
      e => e.props.name.toLowerCase().indexOf(searchInput.toLowerCase()) !== -1
    );
    const mealSearchData = this.renderMealData(sort).filter(
      e => e.props.name.toLowerCase().indexOf(searchInput.toLowerCase()) !== -1
    );
    this.setState({ foodRender: foodSearchData });
    this.setState({ mealRender: mealSearchData });
    if (this.state.foodRender.length === 0 && this.renderFoodData().length !== 0) container.css({ height: 'auto' });
    if (this.state.mealRender.length === 0 && this.renderMealData().length !== 0) container.css({ height: 'auto' });
  };

  render() {
    return (
      <div id='favoritePage'>
        <header id='favoritePageHeader'>
          <div id='favoritePageHeaderContainer'>
            <p id='favoritePageTitle'>المفضلة</p>
            <svg id='favoritePageBack' onClick={this.backButtonHandle} xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
              <path d='M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z' />
            </svg>
          </div>
          <div id='favoritePageTabsContainer'>
            <p id='favoritePageFoodsTab' className='favoritePageTabs' onClick={this.switchTabs}>
              الطعام
            </p>
            <p
              id='favoritePageMealsTab'
              className='favoritePageTabs'
              onClick={this.switchTabs}
              style={{
                opacity:
                  window.location.pathname.includes('CreateNewMeal') || window.location.pathname.includes('EditMeal') ? '0' : '1',
                pointerEvents:
                  window.location.pathname.includes('CreateNewMeal') || window.location.pathname.includes('EditMeal')
                    ? 'none'
                    : 'initial',
              }}
            >
              الوجبات
            </p>
            <div id='paddle'></div>
          </div>
          <svg id='favoritePageSort' onClick={this.sortHandle} xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
            <path d='M3 18h6v-2H3v2zM3 6v2h18V6H3zm0 7h12v-2H3v2z' />
          </svg>
          <svg id='favoriteSearchSvg' onClick={this.toggleSearch} xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
            <circle cx='9.5' cy='9.5' r='6.5' stroke='white' strokeWidth='2' />
            <path d='M15 15L22 22' stroke='white' strokeWidth='2' strokeLinecap='round' />
            <path d='M15 15L22 22' stroke='white' strokeWidth='2' strokeLinecap='round' />
          </svg>
          <div id='favoritePagePopUp'>
            <p
              className='favoritePagePopUpItems'
              id='favoritePagePopUpAB'
              onClick={() => {
                this.setState({ foodRender: this.renderFoodData(true) });
                this.setState({ mealRender: this.renderMealData(true) });
                this.searchOnChangeHandle(true);
                S('#favoritePagePopUp').css({ transform: 'scale(0)' });
              }}
            >
              ترتيب حسب الأبجدية
            </p>
            <p
              className='favoritePagePopUpItems'
              id='favoritePagePopUpNew'
              onClick={() => {
                this.setState({ foodRender: this.renderFoodData(false) });
                this.setState({ mealRender: this.renderMealData(false) });
                this.searchOnChangeHandle(false);
                S('#favoritePagePopUp').css({ transform: 'scale(0)' });
              }}
            >
              ترتيب حسب الأحدث
            </p>
          </div>
        </header>
        <div id='favoriteSearchContainer'>
          <input id='favoriteSearchBar' placeholder='بحث' autoComplete='off' type='search' onChange={this.searchOnChangeHandle} />
        </div>
        <div id='favoritePageBodyContainer'>
          <div id='favoritePageBodyFood'>
            <div id='lottieEmptyFood' style={{ display: this.renderFoodData().length === 0 ? 'block' : 'none' }}>
              <p className='favoritePageIsEmpty'>لا يوجد طعام في المفضلة</p>
            </div>
            <div
              id='lottieNoResultsFood'
              style={{ display: this.state.foodRender.length === 0 && this.renderFoodData().length !== 0 ? 'block' : 'none' }}
            >
              <p className='favoritePageIsEmpty'>لا يوجد نتائج</p>
            </div>

            {this.state.foodRender}
          </div>
          <div id='favoritePageBodymeal'>
            <div id='lottieEmptyMeal' style={{ display: this.renderMealData().length === 0 ? 'block' : 'none' }}>
              <p className='favoritePageIsEmpty'>لا يوجد وجبات في المفضلة</p>
            </div>
            <div
              id='lottieNoResultsMeal'
              style={{ display: this.state.mealRender.length === 0 && this.renderMealData().length !== 0 ? 'block' : 'none' }}
            >
              <p className='favoritePageIsEmpty'>لا يوجد نتائج</p>
            </div>

            {this.state.mealRender}
          </div>
        </div>
      </div>
    );
  }
}
